//Feedback substitute commands for feedback template
//@ means substitute this entry field into the feedback template and if no value in field say this in feedback, 
//? means substitute this entry field into the feedback template if the value exists,
//+bos adds period to end sentence, two spaces and capitalizes first letter of what follows
//+eos adds a period with no preceding spaces

const assessmentEntries = {
    // state: [categorization for feedback, feedback template]
    "contraindicated": ["Problem", "This is contraindicated +bos @contraindicatedWhy +eos"],
    "missingMinimal": ["Problem", "This is recommended by protocol @protocol +bos @why +eos"],
    "missingOptional": ["Caution", "This is according to @protocol +bos but you could do it because @why +eos"],
    "missingRequired": ["Problem", "This is recommended by protocol @protocol +bos @why +eos"],
    "missingStandard": ["Problem", "This is recommended by protocol @protocol +bos @why +eos"],
    "missingAssessment": ["Problem", "You did not do this assessment. +bos ?assessmentFB +eos"],
    "notNecessary": ["Caution", "This was unnecessary +bos @why +eos"],
    "assessment": ["Good", ""],
    "assessment-option": ["Good", ""],
    "decision-option": ["Good", ""],
    "required-action": ["Good", ""],
    "assessment-option-incorrect": ["Problem", "You chose the wrong assessment option +bos @incorrectAnswerFB +eos "],
    "decision-option-incorrect": ["Problem", "You chose the wrong decision option +bos @incorrectAnswerFB +eos "],
    "standard": ["Good", ""],
    "standard-incorrect-answers": ["Problem", "The intervention is correct but there is a problem with the details you provided +bos @incorrectAnswersFB +eos"],
    "minimal-incorrect-answers": ["Problem", "This would solve the problem but does not follow protocol @protocol +bos @minimalWhy +bos also there is a problem with the details you provided +bos @incorrectAnswersFB +eos"],
    "minimal": ["Caution", "You solved the problem but did not follow protocol @protocol +bos @minimalWhy +eos"],
    "green": ["Good", ""],  //just for headers
    "red": ["Problem", ""], //just for headers
    "yellow": ["Caution", ""], //just for headers
    //"notFound": ["Not implemented", "Assessment knowledge is not yet specified", []],
    "default": ["Not implemented", "Possible system issue.  This was not noted in the expert solution."]
}

const getAssessmentEntry = (currentEntry,aIndex) => {
    let assessmentEntry = ""
    if (!assessmentEntries[currentEntry.status]){
        assessmentEntry = assessmentEntries["default"]}
    else{assessmentEntry = assessmentEntries[currentEntry.status]}
    return assessmentEntry[aIndex];
}

export const getAssessmentIcon = (currentEntry) => {  
    let val = iconTypes[getAssessmentEntry(currentEntry,0)]
    return val;
}

export const getColor = (currentEntry) => {
    return colorTypes[getAssessmentEntry(currentEntry,0)];
}

//add protexted spaces after a . except for the very last .  
//Otherwise html reduces continguous regular spaces to a single space.
//Need this for text strings that will appear in one array entry because
//of substitution
const fixStrSp = (textString) => {
    let textArray = textString.split(" ");
    let lenTextArray = textArray.length;
    textArray.forEach((item, i) => {
        let newItem = item.trim()
        if (newItem[item.length-1] === "." && i < lenTextArray-1){
            //add protected space to the string
            newItem = newItem + "\xa0\xa0"}
        textArray[i] = newItem 
    })
    let newString
    for (let item of textArray){
        if (!!newString){
          newString = newString + " " + item;}
        else{newString = item}

    }
    //console.log(newString, textArray)
    return newString
}

export const getFeedback = (currentEntry) => {
    let feedbackTemplate = fixStrSp(getAssessmentEntry(currentEntry,1)).trim()
    //Find any @ actions in the template and substitute with the indicated entry field
    let feedbackArray = feedbackTemplate.split(" ");
    //Process all @ substitutions first since +eos does one forward action
    feedbackArray.forEach((item, i) => {
        let firstChar = item.charAt(0)
        let newString = item.substring(1)
        //trim off excess blanks before and after each word
        feedbackArray[i] = feedbackArray[i].trim()
        switch(firstChar){
            case "@":
                if (!!currentEntry[newString])
                    //replace eos spacing with protected spaces in the substitution string
                    { feedbackArray[i] = fixStrSp(currentEntry[newString]) }
                else {
                    feedbackArray[i] = "[value for " + newString + " is missing]" }
                break
            case "?":
                if (!!currentEntry[newString])
                //replace eos spacing with protected spaces in the substitution string
                    { feedbackArray[i] = fixStrSp(currentEntry[newString]) }
                else {
                    feedbackArray[i] = ""  
                    if (feedbackArray[i-1]  === "+bos"){feedbackArray[i-1] = ""}
                    if (feedbackArray[i+1]  === "+eos"){feedbackArray[i+1] = ""}
                }
                break  
            default:
        }
    })
    //Now address the + actions in the template
    feedbackArray.forEach((item, i) => {
        let firstChar = item.charAt(0)
        let previousString = ""
        switch(firstChar){
        case "+":
            let afterPlusChar = item.substring(1)
            switch(afterPlusChar){
            case "eos":
                //removes existing/redundant . if present so can be certain putting in single . 
                previousString = feedbackArray[i-1].trim();
                if (previousString[previousString.length-1] === "."){
                    previousString = previousString.slice(0,-1);}
                //now add end of sentence and a protected space.  When stiched back into
                //a single string a protected space will be added between each substring
                feedbackArray[i-1] = previousString + ".\xa0"
                feedbackArray[i] = ""  //get rid of the command since was applied
                break
            case "bos":
                //removes existing/redundant . if present so can put in single .
                previousString = feedbackArray[i-1].trim();
                if (previousString[previousString.length-1] === "."){
                    previousString = previousString.slice(0,-1);}
                //now add end of sentence .
                feedbackArray[i-1] = previousString + ".\xa0"
                feedbackArray[i] = "" //get rid of the command since was applied
                //Captitalize the beginning of the sentence
                let nextString = feedbackArray[i+1].trim()
                feedbackArray[i+1] = nextString.charAt(0).toUpperCase() + nextString.slice(1);
                break
            default:
            }
            break
        default:
    }}
    )
    //stitch back into a single string
    let feedbackString
    for (let item of feedbackArray){
        if (!!feedbackString) {
            if (item !== ""){
                feedbackString = feedbackString + "\xa0" + item;}} 
        else {if (item !== ""){feedbackString = item}}
    }
    //console.log(feedbackString)
    return feedbackString
}
    


export const getTimestamp = (currentEntry) => {
    if (currentEntry.status === "criticalActionHeader") {
        return "";
    }
    if (currentEntry.timestamp === -1) {
        return "N/A";  // formatAsMinSecs would convert this to 59:59
    }
    return formatAsMinSecs(currentEntry.timestamp);
}

